@import url("https://fonts.googleapis.com/css2?family=Genos:ital,wght@0,400;0,700;1,400;1,700&family=Handlee&family=Kalam:wght@400;700&family=Rampart+One&family=Righteous&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap");

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    scroll-behavior: smooth;
    font-family: "Montserrat", sans-serif;
}

:root {
    --bg-color: #f6f5f0;
    --second-bg-color: #efede4;
    --text-color: #282120;
    --second-color: #3c1c5b;
    --main-color: #e3606e;
    --icon-color: #f9d876;
    --second-icon-color: #74bdcb;
    --big-font: 4rem;
    --h2-font: 2.5rem;
    --h3-font: 1.5rem;
    --p-font: 1.2rem;
}

body {
    background: var(--bg-color);
    color: var(--text-color);
    margin: auto;
}

section {
    margin-bottom: 10rem;
}

/****** Header Styles ******/
header {
    position: fixed;
    width: 100%;
    top: 0;
    right: 0;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: transparent;
    padding: 22px 15%;
    border-bottom: 1px solid transparent;
    transition: all 0.45s ease;
}

header.sticky {
    background-color: var(--second-bg-color);
    padding: 12px 15%;
}
.logo img {
    width: 4rem;
    border-radius: 100%;
}

.navlist {
    display: flex;
}

.navlist a {
    color: var(--second-color);
    font-family: "Righteous", cursive;
    font-size: 17px;
    font-weight: 500;
    margin: 0 25px;
    transition: all 0.45s ease;
}

.navlist a:hover {
    color: var(--main-color);
}

.navlist a.active {
    color: var(--main-color);
}

#menu-icon {
    font-size: 35px;
    color: var(--text-color);
    z-index: 10001;
    cursor: pointer;
    margin-left: 25px;
    display: none;
}

/****** About Styles ******/
#about .content-wrapper {
    align-items: center;
    justify-content: center;
    display: flex;
}
.about-info {
    width: 90%;
    position: absolute;
    top: 20vh;
    display: flex;
    gap: 2rem;
    align-items: center;
}

.avatar {
    width: 100%;
}

.avatar img {
    width: 100%;
    border-radius: 50%;
    padding: 2rem;
}

.description h1 {
    font-family: "Rampart One", cursive;
    font-size: var(--big-font);
    color: #0000ae;
    line-height: 1.5;
    margin-bottom: 2rem;
}

.description h1 span {
    font-family: "Righteous", cursive;
    font-size: var(--h2-font);
    color: var(--main-color);
    line-height: 1.5;
}

.description h2 {
    font-family: "Kalam", cursive;
    font-size: var(--h2-font);
    color: var(--second-color);
    line-height: 1.5;
}

.description p {
    font-size: var(--p-font);
    color: var(--text-color);
    line-height: 1.5;
    padding: 0.25rem 0;
    margin: 1rem 1rem 1rem 0;
}

.fa-face-smile {
    font-size: var(--p-font);
    color: #0000ae;
}

/****** Skill Styles ******/
#skills h2 {
    font-family: "Kalam", cursive;
    font-size: var(--h2-font);
    color: var(--second-color);
    text-align: center;
    padding: 25px 0;
}

.move-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.skill-list {
    font-size: var(--p-font);
    width: 200px;
    text-align: center;
    align-items: center;
    padding: 10px;
    margin: 18px 0 40px;
    // animation: move 5s linear 2s infinite alternate;
}

.skill-list i {
    font-size: 5rem;
    color: var(--second-icon-color);
}

.skill-list img {
    padding: 0.5rem;
    width: 5rem;
    border-radius: 10px;
    background-color: var(--second-icon-color);
}

.skill-list h3 {
    font-size: var(--p-font);
    color: var(--text-color);
}
