/*** Projects Styles ***/

#projects h2 {
    font-family: "Kalam", cursive;
    font-size: var(--h2-font);
    color: var(--second-color);
    text-align: center;
    padding: 25px 0;
}

.projects-wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(450px, auto));
    justify-items: center;
    justify-content: center;
    gap: 2rem;
    margin-top: 2rem;
}

.project-item {
    width: 100%;
    max-width: 400px;
    height: 500px;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    align-items: center;
    background-color: #fcee96;
    padding: 25px 35px;
    border-radius: 10px;
    transition: all 0.45s ease;
}

.project-item:hover {
    transform: translateY(-9px);
}

.project-item h3 {
    font-family: "Righteous", cursive;
    font-size: var(--h3-font);
    color: var(--second-color);
    text-align: center;
    justify-content: center;
    margin-bottom: 2rem;
}

.project-item img {
    width: 100%;
    margin-bottom: 1rem;
}

.project-language {
    font-size: var(--p-font);
    font-family: "Kalam", cursive;
    font-weight: 500;
    line-height: 2;
    text-decoration: underline;
}
.project-description {
    line-height: 1.5;
}

.project-link {
    background-color: var(--second-color);
    color: #f6f5f0;
    font-size: var(--p-font);
    text-align: center;
    border-radius: 18px;
    padding: 8px;
    margin-top: 2rem;
    transition: all 0.45s ease;
}

.project-link:hover {
    letter-spacing: 1px;
    background-color: var(--bg-color);
    color: var(--main-color);
    border: 1px solid var(--main-color);
}

/*** Contact Styles ***/
#contact {
    padding: 3rem 0;
    margin-bottom: 2rem;
    background-color: #ffff;
}

#contact h2 {
    font-family: "Kalam", cursive;
    font-size: var(--h2-font);
    color: var(--second-color);
    text-align: center;
    padding: 25px 0;
}

address {
    margin: 10px;
    text-align: center;
}

address a {
    font-family: "Handlee", cursive;
    font-weight: 700;
    font-size: var(--h3-font);
    color: var(--main-color);
    line-height: 2;
    transition: all 0.45s ease;
}

address a:hover {
    letter-spacing: 1px;
    background-color: var(--main-color);
    color: whitesmoke;
    border-radius: 30px;
    padding: 1rem;
}

.icons {
    margin-top: 1rem;
    text-align: center;
    align-items: center;
    justify-content: space-between;
}
.icons ul li {
    color: var(--main-color);
    font-size: 3rem;
    display: inline-flex;
}

.icons i {
    color: var(--main-color);
    padding: 0 2rem;
    transition: all 0.45s ease-in-out;
}

.icons i:hover {
    color: #e1c2f0;
}

/*** Footer Styles ***/
#footer {
    margin-bottom: 1.5rem;
    text-align: center;
    justify-content: center;
}

.footer-logo {
    color: var(--second-color);
    font-family: "Handlee", cursive;
    font-size: var(--p-font);
    font-weight: 700;
    letter-spacing: 1px;
}
.footer-logo span {
    font-family: "Handlee", cursive;
    color: #0000ae;
}

.footer-logo:hover {
    border-bottom: 3px solid #f9d876;
}

/*** @media Styles ***/

@media screen and (prefers-reduced-motion: reduce) {
    .box-area li {
        /* Stop the animation */
        animation: none;
    }
}

@media (max-width: 1100px) {
    :root {
        --big-font: 3.5rem;
        --h2-font: 1.5rem;
        --p-font: 0.85rem;
    }

    .animation-area {
        height: 90vh;
    }

    .box-area {
        height: 90%;
    }

    .skill-list {
        padding: 4px;
    }

    .skill-list i {
        font-size: 4rem;
    }
    .skill-list img {
        padding: 0.4rem;
        width: 4rem;
    }
}

@media (max-width: 820px) {
    #menu-icon {
        display: block;
    }
    .navlist {
        position: absolute;
        top: -1000px;
        right: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        background-color: var(--bg-color);
        text-align: center;
        transition: all 0.55s;
    }
    .navlist a {
        display: block;
        margin: 17px;
        font-size: 18px;
        transition: all 0.45s ease;
        color: var(--text-color);
    }
    .navlist a:hover {
        color: var(--main-color);
    }
    .navlist a.active {
        color: var(--main-color);
    }
    .navlist.open {
        top: 100%;
    }

    .about-info {
        top: 10vh;
        display: flex;
        flex-direction: column-reverse;
    }
    .avatar {
        align-items: center;
        display: flex;
        justify-content: center;
    }
    .avatar img {
        width: 40%;
    }
    .description {
        width: 70%;
        text-align: justify;
    }
}

@media (max-width: 650px) {
    :root {
        --big-font: 1.5rem;
        --h2-font: 1rem;
        --p-font: 0.5rem;
    }

    .logo img {
        width: 2rem;
    }
    #menu-icon {
        font-size: 1rem;
    }

    .animation-area {
        height: 70vh;
    }

    .box-area {
        height: 70%;
    }

    .skill-list {
        font-size: var(--p-font);
        width: 170px;
        text-align: center;
        align-items: center;
        padding: 10px;
        margin: 18px 0 40px;
        animation: none;
    }

    .skill-list {
        padding: 2px;
    }

    .skill-list i {
        font-size: 2rem;
    }
    .skill-list img {
        padding: 0.3rem;
        width: 2rem;
    }

    .projects-wrapper {
        grid-template-columns: repeat(auto-fit, minmax(350px, auto));
        gap: 2rem;
    }

    .project-item {
        max-width: 350px;
        height: 450px;
        text-align: center;
    }
}
